import React from 'react';
import { Switch, Redirect } from 'react-router-dom';

import { RouteWithLayout } from './components';
import { Main as MainLayout, Minimal as MinimalLayout } from './layouts';

import {
  Dashboard as DashboardView,
  ProviderList as ProviderListView,
  StockList as StockListView,
  Sales as SaleView,
  ProductList as ProductListView,
  SalesHistory as SalesHistoryView,
  Clients as ClientsView,
  PurchaseOrder as PurchaseOrderView,
  NotFound as NotFoundView
} from './views';

const Routes = () => {
  return (
    <Switch>
      <Redirect
        exact
        from="/"
        to="/dashboard"
      />
      <RouteWithLayout
        component={DashboardView}
        exact
        layout={MainLayout}
        path="/dashboard"
      />
      <RouteWithLayout
        component={StockListView}
        exact
        layout={MainLayout}
        path="/inventario"
      />
      <RouteWithLayout
        component={ProductListView}
        exact
        layout={MainLayout}
        path="/productos"
      />
      <RouteWithLayout
        component={ProviderListView}
        exact
        layout={MainLayout}
        path="/proveedores"
      />
      <RouteWithLayout
        component={PurchaseOrderView}
        exact
        layout={MainLayout}
        path="/orden-de-compra"
      />
      <RouteWithLayout
        component={ClientsView}
        exact
        layout={MainLayout}
        path="/clientes"
      />
      <RouteWithLayout
        component={SaleView}
        exact
        layout={MainLayout}
        path="/ventas"
      />
      <RouteWithLayout
        component={SalesHistoryView}
        exact
        layout={MainLayout}
        path="/historial"
      />
      <RouteWithLayout
        component={NotFoundView}
        exact
        layout={MinimalLayout}
        path="/not-found"
      />
      <Redirect to="/not-found" />
    </Switch>
  );
};

export default Routes;
