import React, { Component } from 'react';
import { Button, Modal, ModalHeader, ModalBody, ModalFooter, Row, Col } from 'reactstrap';
import TextField from '@material-ui/core/TextField';
import { Typography } from '@material-ui/core';
import Alert from '@material-ui/lab/Alert';

export default class AlertInput extends Component {
    constructor(props) {
        super(props)
        this.state = {
            inputQuantity: 1
        }
    }

    handleChange = event => {
        const value = event.target.value;
        this.setState({
            ...this.state,
            [event.target.name]: value
        });
    }
    
    onSubmitQuantity = (e) => {
        e.preventDefault()
        this.props.onConfirmAddProduct(this.state.inputQuantity,this.props.value)
    }
    render() {
        const { modalTitle, isOpen, value } = this.props;
        return (
            <Modal isOpen={isOpen} zIndex={1211} centered toggle={this.props.toggle}>
                <form onSubmit={this.onSubmitQuantity}>
                    <ModalHeader>{modalTitle}</ModalHeader>
                    <ModalBody>
                        {value &&
                            <div className='row'>
                                <div className='col-12'>
                                    <Typography variant='h5' gutterBottom>
                                        Articulo: {value.nombre}
                                    </Typography>
                                    {value.unidad_de_medida &&
                                        <div>
                                            <Typography variant='h5' gutterBottom>
                                                Unidad de medida: {value.unidad_de_medida && value.unidad_de_medida}
                                            </Typography>

                                            <Alert severity='warning' icon={false}>Utilice una cantidad correcta en base a la unidad de medida que se muestra arriba, un error en este dato puede ocacionar inconsistencias en el sistema. </Alert>
                                        </div>
                                    }

                                    <Typography variant='subtitle2' gutterBottom>
                                        Cantidad {value.unidad_de_medida && 'en ' + value.unidad_de_medida + 's'}
                                    </Typography>
                                </div>
                            </div>
                        }
                        <TextField
                            fullWidth
                            id="outlined-number"
                            label="Cantidad"
                            type="number"
                            min='1'
                            name='inputQuantity'
                            value={this.state.inputQuantity}
                            onChange={this.handleChange}
                            variant="outlined"
                            required
                        />
                    </ModalBody>
                    <ModalFooter>
                        <Button color='primary' >Agregar cantidad</Button>{' '}
                        <Button color='secondary' onClick={this.props.cancelModal} >Cancelar</Button>
                    </ModalFooter>
                </form>
            </Modal>
        );
    }
}