import React, { forwardRef } from 'react';

import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';


import AddBox from '@material-ui/icons/AddBox';
import ArrowDownward from '@material-ui/icons/ArrowDownward';
import Check from '@material-ui/icons/Check';
import ChevronLeft from '@material-ui/icons/ChevronLeft';
import ChevronRight from '@material-ui/icons/ChevronRight';
import Clear from '@material-ui/icons/Clear';
import DeleteOutline from '@material-ui/icons/DeleteOutline';
import Edit from '@material-ui/icons/Edit';
import FilterList from '@material-ui/icons/FilterList';
import FirstPage from '@material-ui/icons/FirstPage';
import LastPage from '@material-ui/icons/LastPage';
import Remove from '@material-ui/icons/Remove';
import SaveAlt from '@material-ui/icons/SaveAlt';
import Search from '@material-ui/icons/Search';
import ViewColumn from '@material-ui/icons/ViewColumn';

export default {
    columns: [
        { title: 'Id', field: 'id_stock', type: 'numeric', editable: 'never'},
        { title: 'Nombre', field: 'nombre' },
        { title: 'Cantidad en inventario', field: 'cantidad', type: 'numeric' },
        { title: 'Descripcin', field: 'descripcion' },
        { title: 'Precio', field: 'precio', type: 'numeric' },
        { title: 'Provedor', field: 'id_proveedor', type: 'numeric', editable: 'never'},
        { title: 'Fecha de compra', field: 'fecha_de_compra', type: 'date' },
        { title: 'Fecha de caducidad', field: 'fecha_de_caducidad', type: 'date' },
        { title: 'Das de soporte', field: 'dias_de_soporte', type: 'numeric' },
        { title: 'Unidad de medida', field: 'unidad_de_medida', lookup: { 'Kilogramo': 'Kilogramo', 'Pieza': 'Pieza', 'Litro': 'Litro' } },
    ],
    options: {
        exportButton: true,
        actionsColumnIndex: 3
    },
    detailPanel: rowData => {
        return (
            <Table size="small" aria-label="Ms informacin">
                <TableHead>
                    <TableRow>
                        <TableCell>Date</TableCell>
                        <TableCell>Customer</TableCell>
                        <TableCell align="right">Amount</TableCell>
                        <TableCell align="right">Total price ($)</TableCell>
                    </TableRow>
                </TableHead>
                <TableBody>
                    {/* {row.history.map((historyRow) => ( */}
                        <TableRow key={'historyRow.date'}>
                            <TableCell component="th" scope="row">
                                {'historyRow.date'}
                            </TableCell>
                            <TableCell>{'historyRow.customerId'}</TableCell>
                            <TableCell align="right">{'historyRow.amount'}</TableCell>
                            <TableCell align="right">
                                {'Math.round(historyRow.amount * row.price * 100) / 100'}
                            </TableCell>
                        </TableRow>
                    {/* ))} */}
                </TableBody>
            </Table>
        )

    },
    actions: [
        rowData => ({
            icon: forwardRef((props, ref) => <AddBox {...props} ref={ref} />),
            tooltip: 'Agregar articulos',
            onClick: (event, rowData) => { console.log(rowData) },
            disabled: rowData.birthYear < 2000
        })
    ]
    ,
    localization: {
        pagination: {
            labelDisplayedRows: '{from}-{to} de {count}',
            labelRowsSelect: 'filas',
            labelRowsPerPage: 'Filas por pgina:',
            firstAriaLabel: 'Primera pgina',
            firstTooltip: 'Primera pgina',
            previousAriaLabel: 'Pgina anterior',
            previousTooltip: 'Pgina anterior',
            nextAriaLabel: 'Siguiente pgina',
            nextTooltip: 'Siguiente pgina',
            lastAriaLabel: 'ltima pgina',
            lastTooltip: 'ltima pgina'
        },
        toolbar: {
            nRowsSelected: '{0} fila(s) seleccionadas',
            exportTitle: 'Exportar',
            exportAriaLabel: 'Exportar',
            exportName: 'Exportar como CSV',
            searchTooltip: 'Buscar',
            searchPlaceholder: 'Buscar'
        },
        header: {
            actions: 'Acciones'
        },
        body: {
            emptyDataSourceMessage: 'No hay informacin que mostrar',
            filterRow: {
                filterTooltip: 'Filtro'
            },
            addTooltip: 'Agregar',
            deleteTooltip: 'Borrar',
            editTooltip: 'Editar',
            editRow: {
                deleteText: 'Estas seguro de eliminar esta fila?',
                cancelTooltip: 'Cancelar',
                saveTooltip: 'Guardar'
            },

        }


    },
    tableIcons: {
        Add: forwardRef((props, ref) => <AddBox {...props} ref={ref} />),
        Check: forwardRef((props, ref) => <Check {...props} ref={ref} />),
        Clear: forwardRef((props, ref) => <Clear {...props} ref={ref} />),
        Delete: forwardRef((props, ref) => <DeleteOutline {...props} ref={ref} />),
        DetailPanel: forwardRef((props, ref) => <ChevronRight {...props} ref={ref} />),
        Edit: forwardRef((props, ref) => <Edit {...props} ref={ref} />),
        Export: forwardRef((props, ref) => <SaveAlt {...props} ref={ref} />),
        Filter: forwardRef((props, ref) => <FilterList {...props} ref={ref} />),
        FirstPage: forwardRef((props, ref) => <FirstPage {...props} ref={ref} />),
        LastPage: forwardRef((props, ref) => <LastPage {...props} ref={ref} />),
        NextPage: forwardRef((props, ref) => <ChevronRight {...props} ref={ref} />),
        PreviousPage: forwardRef((props, ref) => <ChevronLeft {...props} ref={ref} />),
        ResetSearch: forwardRef((props, ref) => <Clear {...props} ref={ref} />),
        Search: forwardRef((props, ref) => <Search {...props} ref={ref} />),
        SortArrow: forwardRef((props, ref) => <ArrowDownward {...props} ref={ref} />),
        ThirdStateCheck: forwardRef((props, ref) => <Remove {...props} ref={ref} />),
        ViewColumn: forwardRef((props, ref) => <ViewColumn {...props} ref={ref} />)
    }
};