import React, { Component } from 'react';
import PageTitleBar from 'components/PageTitleBar';
import ModalProduct from './components/ModalProduct';
import Button from '@material-ui/core/Button';
import AddCircleIcon from '@material-ui/icons/AddCircle';

class ProductList extends Component {

  constructor(props) {
    super(props);
    this.state = {
      modalProductTitle: 'Crear producto',
      modalProductOpen: true
    }
  }

  render() {
    return (
      <div>
        <PageTitleBar title={'Productos'} description={'Productos a la venta'} />
        <div className={'m-3'}>
          <div className={'m-2'}>
            <div className='mb-3'>
              <Button
                variant="contained"
                color="secondary"
                startIcon={<AddCircleIcon />}
                onClick={() => this.setState({modalProductOpen: true})}
              >
                Crear producto.
              </Button>
            </div>
          </div>
        </div>
        <ModalProduct
          modalTitle={this.state.modalProductTitle}
          isOpen={this.state.modalProductOpen}
          cancelModal={() => { this.setState({ modalProductOpen: false }) }} />
      </div>
    );
  }
}

export default ProductList;
