import React, { Component } from 'react';
import PageTitleBar from 'components/PageTitleBar';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import ArrowForwardIcon from '@material-ui/icons/ArrowForward';
import AddCircleIcon from '@material-ui/icons/AddCircle';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import TabPanel from './components/TabPanel';
import { Row, Col } from 'reactstrap';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import products from './conf';
import ScrollMenu from 'react-horizontal-scrolling-menu';
import Typography from '@material-ui/core/Typography';
import SweetAlert from 'react-bootstrap-sweetalert';
import './style.scss';

const Arrow = ({ text, className }) => {
    return (
        <IconButton aria-label="delete" className={className}>
            {className === 'arrow-prev' ?
                <ArrowBackIcon />
                :
                <ArrowForwardIcon />
            }
        </IconButton>
    );
};


const ArrowLeft = Arrow({ text: '<', className: 'arrow-prev' });
const ArrowRight = Arrow({ text: '>', className: 'arrow-next' });

export default class Sales extends Component {
    constructor(props) {
        super(props);

        this.state = {
            value: 0,
            valueCategory: 0,
            tabs: [],
            tabContent: [],
            selected: null,
            productsMenu: [],
            addProductAlert: null
        }
        
    }

    componentDidMount() {
        /*         this.getSales() */
        let tab = this.state.tabs;
        let tabC = this.state.tabContent
        tab.push({ label: 'Venta ' + 1 })
        tabC.push({ content: 'Nuevo ' + 0 })
        this.setState({
            tabs: tab,
            tabContent: tabC
        })
        let products = this.setProductsInitialization()
        this.setState({
            productsMenu: products
        })
    }

    addProduct = (pro) => {
        const imagen = pro.imagen ? '' + pro.imagen : '/images/products/no-image.svg'
        this.setState({
            addProductAlert: <SweetAlert
                input
                inputType='number'
                validationRegex={new RegExp('^[0-9]+$')}
                validationMsg="Valor no valido"
                showCancel
                cancelBtnText='Cancelar'
                confirmBtnText='Agregar artculo'
                confirmBtnBsStyle="primary"
                cancelBtnBsStyle="default"
                btnSize="sm"
                title={pro.nombre}
                placeHolder="cantidad"
                onConfirm={(response) => { this.onConfirmAddProduct(response) }}
                onCancel={this.onCancelAddProduct}
            >
                <div className='row'>
                    <div className='col-6'>
                        <div className="img-product-alert" style={{ backgroundImage: 'url('+ imagen + ')' }}>
                        </div>
                    </div>
                    <div className='col-6 no-center'>
                        <Typography variant="h2" gutterBottom>${pro.precio}</Typography>
                        <Typography variant="body1" gutterBottom>{pro.descripcion}</Typography>
                    </div>
                    <div className='col-12 no-center'>
                        <Typography variant='subtitle2' gutterBottom>
                            Cantidad
                        </Typography>
                    </div>
                </div>
            </SweetAlert>
        })
    }

    onConfirmAddProduct = (response) => {
        console.log(response);
    }

    onCancelAddProduct = () => {
        this.setState({
            addProductAlert: null
        })
    }

    setProductsInitialization = () => {
        return products.categorias.map((categoria, key) => {
            return categoria.productos.map((pro, key) => {
                return (
                    <Card key={key} style={{ width: '150px', minHeight: '240px' }} className='m-3'>
                        <CardActionArea style={{ maxHeight: '210px', minHeight: '200px' }} onClick={() => this.addProduct(pro)}>
                            <CardMedia
                                component="img"
                                alt={pro.nombre}
                                height="100"
                                image={pro.imagen ? "/images/products/burguer.jpg" : "/images/products/no-image.svg"}
                                title={pro.nombre}
                            />
                            <CardContent style={{ minHeight: '100px', maxHeight: '100px' }}>
                                <Typography gutterBottom variant="h5" component="h2">
                                    {pro.nombre}
                                </Typography>
                                <Typography color="textSecondary">
                                    $ {pro.precio}
                                </Typography>
                                <Typography variant="body2" color="textSecondary" component="p">
                                    {pro.descripcion && pro.descripcion.length > 30 ?
                                        pro.descripcion.substring(0, 30) + ' ...'
                                        :
                                        pro.descripcion
                                    }
                                </Typography>
                            </CardContent>
                        </CardActionArea>
                    </Card>
                );
            })
        })
    }

    onSelect = key => {
        this.setState({ selected: key });
    }

    addTab = () => {
        let tab = this.state.tabs;
        let tabC = this.state.tabContent
        tab.push({ label: 'Venta ' + (tab.length + 1) })
        tabC.push({ content: 'Nuevo ' + tabC.length })
        this.setState({
            tabs: tab,
            tabContent: tabC,
            value: tab.length - 1
        })
    }

    handleChange = (event, newValue) => {
        this.setState({
            value: newValue
        }, () =>
            console.log(this.state)
        )
    }

    handleChangeCategory = (event, newValue) => {
        this.setState({
            valueCategory: newValue
        }, () =>
            console.log(this.state)
        )
    }

    setIds = (index) => {
        return {
            id: `full-width-tab-${index}`,
            'aria-controls': `full-width-tabpanel-${index}`,
        };
    }

    getSaleMenu = (value) => {
        return <TabPanel key={value} value={this.state.value} index={value}>
            {value}
        </TabPanel>
    }

    render() {
        const { value, valueCategory, selected, addProductAlert } = this.state
        return (
            <div>
                {addProductAlert}
                <PageTitleBar title='Ventas' description='Vista para agregar ventas ' />
                <div className={'m-3'}>
                    <div className={'m-2'}>
                        <div className='mb-3'>
                            <Button
                                variant="contained"
                                color="secondary"
                                startIcon={<AddCircleIcon />}
                                className={'shadow-none'}
                                onClick={this.addTab}
                            >
                                Agregar venta.
                        </Button>
                        </div>
                        <AppBar position="static" color="default" style={{ flexGrow: 1 }}>
                            <Tabs
                                value={this.state.value}
                                onChange={this.handleChange}
                                indicatorColor="primary"
                                textColor="primary"
                                variant="scrollable"
                                scrollButtons="auto"
                                aria-label="scrollable auto tabs example"
                            >
                                {
                                    this.state.tabs && this.state.tabs.map((tab, key) => {
                                        return <Tab key={key} label={tab.label} {...this.setIds(key)} />;
                                    })
                                }
                            </Tabs>
                        </AppBar>
                        {
                            this.state.tabContent && this.state.tabContent.map((tabContent, key) => {
                                return (
                                    <TabPanel key={key} value={value} index={key}>
                                        <Row>
                                            <Col xs="12" sm="12" >
                                                <h4>Productos</h4>
                                                <AppBar position="static" color='default'>
                                                    <Tabs
                                                        value={valueCategory}
                                                        variant="scrollable"
                                                        scrollButtons="auto"
                                                        onChange={this.handleChangeCategory}
                                                        aria-label="simple tabs example"
                                                    >
                                                        {
                                                            products.categorias.map((categoria, key) => {
                                                                return (
                                                                    <Tab key={key} label={categoria.nombre} {...this.setIds(key)} />
                                                                );
                                                            })
                                                        }
                                                    </Tabs>
                                                </AppBar>
                                                {products.categorias.map((categoria, key) => {
                                                    return (
                                                        <div role="tabpanel"
                                                            key={key}
                                                            hidden={valueCategory !== key}
                                                            id={`scrollable-auto-tabpanel-${key}`}
                                                            aria-labelledby={`scrollable-auto-tab-${key}`}
                                                            style={{ backgroundColor: '#fff' }}
                                                        >
                                                            {valueCategory === key && (
                                                                <Col sm='12'>
                                                                    <div>
                                                                        <ScrollMenu
                                                                            data={this.state.productsMenu[key]}
                                                                            arrowLeft={ArrowLeft}
                                                                            arrowRight={ArrowRight}
                                                                            selected={selected}
                                                                            onSelect={this.onSelect}
                                                                            alignCenter={false}
                                                                            wheel={false}
                                                                        />
                                                                    </div>
                                                                </Col>
                                                            )}
                                                        </div>

                                                    );
                                                })}
                                            </Col>
                                            <Col xs="12" sm="12" className='mt-xs-3'>
                                                <h4>Venta</h4>
                                                <TableContainer >
                                                    <Table size='small' aria-label="venta">
                                                        <TableHead>
                                                            <TableRow>
                                                                <TableCell>Producto</TableCell>
                                                                <TableCell align="right">Cantidad</TableCell>
                                                                <TableCell align="right">P.U.</TableCell>
                                                                <TableCell align="right">Total</TableCell>
                                                            </TableRow>
                                                        </TableHead>
                                                        <TableBody>
                                                            {
                                                                products.ventas[key] && products.ventas[key].productos.map((product, key2) => {
                                                                    return (
                                                                        <TableRow key={key2}>
                                                                            <TableCell component="th" scope="row">
                                                                                {product.nombre}
                                                                            </TableCell>
                                                                            <TableCell align="right">
                                                                                <input
                                                                                    type='number'
                                                                                    value={product.precio}
                                                                                >

                                                                                </input>
                                                                            </TableCell>
                                                                            <TableCell align="right">{product.cantidad}</TableCell>
                                                                            <TableCell align="right">{product.cantidad * product.precio}</TableCell>
                                                                        </TableRow>
                                                                    );
                                                                })
                                                            }
                                                            <TableRow>
                                                                <TableCell rowSpan={3} />
                                                                <TableCell colSpan={2}>Subtotal</TableCell>
                                                                <TableCell align="right">{100}</TableCell>
                                                            </TableRow>
                                                            <TableRow>
                                                                <TableCell>Tax</TableCell>
                                                                <TableCell align="right">10%</TableCell>
                                                                <TableCell align="right">10</TableCell>
                                                            </TableRow>
                                                            <TableRow>
                                                                <TableCell colSpan={2}>Total</TableCell>
                                                                <TableCell align="right">110</TableCell>
                                                            </TableRow>
                                                        </TableBody>
                                                    </Table>
                                                </TableContainer>
                                            </Col>
                                        </Row>
                                    </TabPanel>
                                );
                            })
                        }
                    </div>
                </div>

            </div>
        );
    }
}