import React, { Component } from 'react';
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
import { default as MaterialButton } from '@material-ui/core/Button';
import { Container, Row, Col } from 'reactstrap';
import { Form, FormGroup, Label, Input } from 'reactstrap';
import TextField from '@material-ui/core/TextField';
import MenuItem from '@material-ui/core/MenuItem';
import API from 'utils/API';
import { Typography } from '@material-ui/core';
import ModalProductTable from '../ModalProductTable';
import SweetAlert from 'react-bootstrap-sweetalert';
import Alert from '@material-ui/lab/Alert';
import ImageUpload from 'components/ImageUpload';
import IconButton from '@material-ui/core/IconButton';
import DeleteIcon from '@material-ui/icons/Delete';
import AlertInput from '../AlertInput';
import InputAdornment from '@material-ui/core/InputAdornment';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import OutlinedInput from '@material-ui/core/OutlinedInput';

export default class ModalBaseProduct extends Component {
    constructor(props) {
        super(props)
        this.state = {
            stockSelect: '',
            stock: [],
            ingredients: [],
            alertQuantity: false,
            alertQuantityItem: undefined,
            productDescription: '',
            productName: ''
        }
    }

    componentDidMount() {
        this.getStock()
    }

    getStock = () => {
        API.get('/get_stock.php')
            .then((response) => {
                response.data.status === 'success' ?
                    this.setState({
                        stock: response.data.data
                    })
                    :
                    console.log('Error')
            })
            .catch((error) => {
                console.log(error)
            })
    }

    getIngredientCost = (measure, price, quantity) => {

        let precio = measure === 'Kilogramo' ?
            //Calculo para kilos
            (price) * quantity
            :
            measure === 'Pieza' ?
                //Calculo para piezas
                price * quantity
                :
                //Calculo para litros
                measure === 'Litro' ?
                    price * quantity
                    :
                    ''
        return precio
    }

    handleSelectIngredient = (event) => {
        const value = event.target.value
        this.setState({
            alertQuantity: true,
            alertQuantityItem: value
        })
    }

    onChangeQuantity = (event, ingredient) => {
        let value = event.target.value
        if (value<0){
            return
        }
        let newData = ingredient.id_stock ? {
            id_stock: ingredient.id_stock,
            name: ingredient.name,
            quantityValue: value,
            price: parseInt(ingredient.price,10),
            cost: value ? ingredient.price && ingredient.measure ? this.getIngredientCost(ingredient.measure, ingredient.price, parseInt(value, 10)): 0 : 0,
            measure: ingredient.measure
        }
            :
            {
                id_stock: ingredient.id_base_product,
                name: ingredient.name,
                quantityValue: value,
                price: parseInt(ingredient.price,10),
                cost: value ? ingredient.price && ingredient.measure ? this.getIngredientCost(ingredient.measure, ingredient.price, parseInt(value, 10)) : 0 : 0,
                measure: ingredient.measure,
            }
        this.setState((prevState) => {
            const ingredients = [...prevState.ingredients];
            ingredients[ingredients.indexOf(ingredient)] = newData;
            return { ...prevState, ingredients };
        })
    }

    onConfirmAddProduct = (value, product) => {

        let verifyIngredient = this.state.ingredients.filter((ingrediente) => {
            return product.id_stock ?
                ingrediente.id_stock === product.id_stock
                :
                ingrediente.id_base_product === product.id_base_product
        })
        if (verifyIngredient.length > 0) {
            verifyIngredient[0].quantityValue = parseInt(verifyIngredient[0].quantityValue, 10) + parseInt(value, 10)
            verifyIngredient[0].cost = verifyIngredient[0].price ? this.getIngredientCost(verifyIngredient[0].measure, verifyIngredient[0].price, parseInt(verifyIngredient[0].quantityValue, 10)) : 0
            this.setState({
                alertQuantity: false,
            })
            return
        }


        let ingredient = {}
        product.id_stock ?
            ingredient = {
                id_stock: product.id_stock,
                name: product.nombre,
                quantityValue: value,
                price: parseInt(product.precio,10),
                cost: product.precio && product.unidad_de_medida ? this.getIngredientCost(product.unidad_de_medida, product.precio, parseInt(value, 10)) : 0,
                measure: product.unidad_de_medida,
            }
            :
            ingredient = {
                id_base_product: product.id_base_product,
                name: product.nombre,
                quantityValue: value,
                price: parseInt(product.precio,10),
                cost: product.precio && product.unidad_de_medida  ? this.getIngredientCost(product.unidad_de_medida, product.precio, parseInt(value, 10)) : 0,
                measure: product.unidad_de_medida,
            }
            console.log(ingredient)
        this.setState({
            alertQuantity: false,
            ingredients: [...this.state.ingredients, ingredient]
        },()=>console.log(this.state.ingredients))
    }

    deleteIngredient = (product) => {
        let newIngredients = this.state.ingredients.filter((ingrediente) => {
            return product.id_stock ?
                ingrediente.id_stock !== product.id_stock && ingrediente.name !== product.nombre
                :
                ingrediente.id_base_product !== product.id_base_product && ingrediente.name !== product.nombre
        })
        this.setState({
            ingredients: newIngredients
        })
    }

    handleChange = event => {
        const value = event.target.value;
        this.setState({
            ...this.state,
            [event.target.name]: value
        });
    }

    onSubmitModal = (e) => {
        e.preventDefault();
        console.log(this.state)
        if (this.state.ingredients.length <= 0){
            alert('No se agregaron ingredientes')
            return
        }
        
        const base_product = {
            nombre: this.state.productName,
            descripcion: this.state.productDescription,
            ingredients: this.state.ingredients,
            precio: this.state.ingredients.reduce((prev, next) => prev + parseInt(next.cost, 10), 0)
        }

        API.post('create_base_product.php',base_product)
        .then((response)=>{
            console.log(response)
        })
        .catch((error)=>{
            console.log(error)
        })
    }

    render() {
        const { productName, productDescription, stockSelect, stock } = this.state
        const { isOpen, modalTitle, } = this.props
        return (<div>
            <Modal isOpen={isOpen} size='lg' zIndex={1211} >
                <ModalHeader>{modalTitle}</ModalHeader>
                <Form onSubmit={this.onSubmitModal} autoComplete='off'>
                    <ModalBody>
                        <Container>

                            <Row>
                                <Col sm='12'>
                                    <Typography variant='subtitle2' gutterBottom>
                                        Informacin general.
                                    </Typography>
                                    <TextField className='mb-3' fullWidth id='productName' label='Nombre' name='productName' variant='outlined' onChange={this.handleChange} value={productName} required />
                                {/* </Col>
                                <Col sm='6'> */}
                                    <TextField
                                        fullWidth
                                        id="productDescription"
                                        label="Descripcin"
                                        multiline
                                        name='productDescription'
                                        rows={5}
                                        value={productDescription}
                                        onChange={this.handleChange}
                                        variant='outlined'
                                    />
                                </Col>
                            </Row>
                            <Row>
                                <Col sm='12'>
                                    <Typography variant='subtitle2' gutterBottom>
                                        Ingredientes.
                                    </Typography>
                                    <TextField
                                        fullWidth
                                        id='stockSelect'
                                        select
                                        label='Articulos de inventario'
                                        name='stockSelect'
                                        value={stockSelect}
                                        onChange={this.handleSelectIngredient}
                                        helperText='Articulos de inventario.'
                                        variant='outlined'
                                    >
                                        {stock && stock.map((stockItem, key) => {
                                            return (
                                                <MenuItem key={key} value={stockItem}>
                                                    {stockItem.nombre}
                                                </MenuItem>
                                            );
                                        })}
                                    </TextField>
                                    <ModalProductTable
                                        products={this.state.ingredients}
                                        onChangeQuantity={this.onChangeQuantity}
                                        deleteIngredient={this.deleteIngredient}
                                    />
                                </Col>
                            </Row>
                        </Container>
                    </ModalBody>
                    <ModalFooter>
                        <Button color='primary' >Agregar producto</Button>{' '}
                        <Button color='secondary' onClick={this.props.cancelModal} >Cancelar</Button>
                    </ModalFooter>
                </Form>
            </Modal>
            <AlertInput
                modalTitle='Agregar cantidad'
                isOpen={this.state.alertQuantity}
                value={this.state.alertQuantityItem}
                cancelModal={() => this.setState({ alertQuantity: false })}
                toggle={() => this.setState({ alertQuantity: !this.state.alertQuantity })}
                onConfirmAddProduct={this.onConfirmAddProduct}
            />
        </div>
        );
    }
}