import React from 'react';
import Helmet from 'react-helmet'
const PageTitleBar = props => {
    return (
        <div className='mt-3 ml-3'>
            <Helmet>
                <title>{props.title}</title>
                <meta name="description" content={props.description} />
            </Helmet>
            <h1>{props.title}</h1>

        </div>
    );
}

export default PageTitleBar;