import React, { Component } from 'react';
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
import Grid from '@material-ui/core/Grid';
import { Container, Row, Col } from 'reactstrap';
import { Form, FormGroup, Label, Input } from 'reactstrap';
import SweetAlert from 'react-bootstrap-sweetalert';
import DateFnsUtils from '@date-io/date-fns';
import {
    MuiPickersUtilsProvider,
    KeyboardTimePicker,
    KeyboardDatePicker,
} from '@material-ui/pickers';
import API from 'utils/API';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';

export default class ModalAddStock extends Component {
    constructor(props) {
        super(props)
        this.state = {
            quantity: 0,
            addDate: new Date(),
            expirationDate: new Date(),
            sweetAlert: null,
        }
    }

    onSubmitModal = (e) => {
        e.preventDefault();
        if (this.state.quantity < 1) {
            this.errorAlert('Cantidad invalida', 'No puede agregar una cantidad menor a 1')
            return
        }
        let quantity = {
            id_stock: this.props.item.id_stock,
            cantidad: this.state.quantity,
            fecha_de_compra: this.state.addDate,
            fecha_de_caducidad: this.state.expirationDate
        }
        API.post('/add_stock_item.php', quantity)
            .then(res => {
                if (res.data.status === 'fail') {
                    this.errorAlert("Error al agregar articulo", res.data.message)
                } else {
                    this.showAlert();
                    this.setState({
                        quantity: 0,
                        addDate: new Date(),
                        expirationDate: new Date()
                    })
                    this.props.cancelModal()
                }
            })
            .catch(error => {
                this.errorAlert("Error al agregar articulos", error.message);
            });
    }


    errorAlert = (title, description) => {
        this.setState({
            sweetAlert: <SweetAlert title={title}
                danger
                onConfirm={() =>
                    this.setState({
                        sweetAlert: null
                    })
                }
                onCancel={() =>
                    this.setState({
                        sweetAlert: null
                    })
                }>
                {description}
            </SweetAlert>
        })
    }

    showAlert = () => {
        this.setState({
            sweetAlert: <SweetAlert title="Proveedor creado correctamente"
                onConfirm={() =>
                    this.setState({
                        sweetAlert: null
                    })
                }
                onCancel={() =>
                    this.setState({
                        sweetAlert: null
                    })
                }>
            </SweetAlert>
        })
    }

    handleChange = event => {
        const value = event.target.value;
        this.setState({
            ...this.state,
            [event.target.name]: value
        });
    }


    render() {
        const { modalTitle, isOpen } = this.props;
        const stockItem = this.props.item
        console.log(this.props)
        return (
            <div>
                <Modal isOpen={isOpen} zIndex={1210} centered >
                    <ModalHeader>{modalTitle}</ModalHeader>
                    <Form onSubmit={this.onSubmitModal}>
                        <ModalBody>
                            <Container>
                                <Row>
                                    <Col sm='12'>
                                        <Typography className='text-center' variant="h4" gutterBottom>
                                            {stockItem && stockItem.nombre}
                                        </Typography>
                                        <TextField
                                            fullWidth
                                            id="quantity"
                                            label="Cantidad a agregar"
                                            type="number"
                                            InputLabelProps={{
                                                shrink: true,
                                            }}
                                            variant="outlined"
                                            name='quantity'
                                            onChange={this.handleChange}
                                        />
                                        <MuiPickersUtilsProvider utils={DateFnsUtils}>
                                            <Grid container justify="space-around">
                                                <KeyboardDatePicker
                                                    fullWidth
                                                    disableToolbar
                                                    variant="normal"
                                                    format="yyyy/MM/dd"
                                                    margin="normal"
                                                    id="addDate"
                                                    label="Fecha de compra"
                                                    value={this.state.addDate}
                                                    onChange={(date) => this.setState({ addDate: date })}
                                                    name='addDate'
                                                    KeyboardButtonProps={{
                                                        'aria-label': 'change date',
                                                    }}
                                                />
                                                <KeyboardDatePicker
                                                    fullWidth
                                                    disableToolbar
                                                    variant="normal"
                                                    format="yyyy/MM/dd"
                                                    margin="normal"
                                                    id="expirationDate"
                                                    name='expirationDate'
                                                    label="Fecha de caducidad"
                                                    value={this.state.expirationDate}
                                                    onChange={(date) => this.setState({ expirationDate: date })}
                                                    KeyboardButtonProps={{
                                                        'aria-label': 'change date',
                                                    }}
                                                />
                                            </Grid>
                                        </MuiPickersUtilsProvider>
                                    </Col>
                                </Row>
                            </Container>
                        </ModalBody>
                        <ModalFooter>
                            <Button color='primary' >Agregar a inventario</Button>{' '}
                            <Button color='secondary' onClick={this.props.cancelModal} >Cancelar</Button>
                        </ModalFooter>
                    </Form>
                </Modal>
                {this.state.sweetAlert}
            </div>
        );
    }
}