import React, { Component } from 'react';
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
import { default as MaterialButton } from '@material-ui/core/Button';
import { Container, Row, Col } from 'reactstrap';
import { Form, FormGroup, Label, Input } from 'reactstrap';
import TextField from '@material-ui/core/TextField';
import MenuItem from '@material-ui/core/MenuItem';
import API from 'utils/API';
import { Typography } from '@material-ui/core';
import ModalProductTable from '../ModalProductTable';
import SweetAlert from 'react-bootstrap-sweetalert';
import Alert from '@material-ui/lab/Alert';
import ImageUpload from 'components/ImageUpload';
import IconButton from '@material-ui/core/IconButton';
import DeleteIcon from '@material-ui/icons/Delete';
import AlertInput from '../AlertInput';
import InputAdornment from '@material-ui/core/InputAdornment';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import OutlinedInput from '@material-ui/core/OutlinedInput';
import ModalBaseProduct from '../ModalBaseProduct'
export default class ModalProduct extends Component {

    constructor(props) {
        super(props)

        this.state = {
            sweetAlert: null,
            productName: '',
            productCategory: '',
            stockSelect: '',
            baseProductSelect: '',
            productDescription: '',
            categories: [],
            stock: [],
            baseProducts: [],
            alertQuantity: false,
            pictures: [],
            ingredients: [],
            baseProductModal: false
        }
    }

    componentDidMount() {
        this.getCategories()
        this.getStock()
        this.getBaseProducts()
    }

    onDrop = (pictureFiles, pictureDataURLs) => {
        this.setState({
            pictures: this.state.pictures.concat(pictureFiles)
        });
    }

    handleSelectIngredient = (event) => {
        const value = event.target.value
        this.setState({
            alertQuantity: true,
            alertQuantityItem: value
        })
    }

    onChangeQuantity = (event, ingredient) => {
        console.log(event.target.value, ingredient)
    }

    getIngredientCost = (measure, price, quantity) => {

        let precio = measure === 'Kilogramo' ?
            //Calculo para kilos
            (price) * quantity
            :
            measure === 'Pieza' ?
                //Calculo para piezas
                price * quantity
                :
                //Calculo para litros
                measure === 'Litro' ?
                    price * quantity
                    :
                    ''
        console.log(precio)
        return precio
    }

    onConfirmAddProduct = (value, product) => {

        let verifyIngredient = this.state.ingredients.filter((ingrediente) => {
            return product.id_stock ?
                ingrediente.id_stock === product.id_stock
                :
                ingrediente.id_base_product === product.id_base_product
        })
        if (verifyIngredient.length > 0) {
            verifyIngredient[0].quantity = parseInt(verifyIngredient[0].quantity, 10) + parseInt(value, 10)
            this.setState({
                alertQuantity: false,
            })
            return
        }


        let ingredient = {}
        product.id_stock ?
            ingredient = {
                id_stock: product.id_stock,
                name: product.nombre,
                quantityValue: value,
                cost: this.getIngredientCost(product.unidad_de_medida, product.precio, parseInt(value, 10)),
                quantity: <FormControl variant="outlined" style={{ width: '12ch' }}>
                    <OutlinedInput
                        id="outlined-adornment-weight"
                        /* value={values.weight} */
                        onChange={(e) => this.onChangeQuantity(e, product)}
                        endAdornment={<InputAdornment position="end">
                            {product.unidad_de_medida === 'Pieza' && 'Pz'}
                            {product.unidad_de_medida === 'Kilogramo' && 'Kg'}
                            {product.unidad_de_medida === 'Litro' && 'L'}
                        </InputAdornment>}
                        defaultValue={value}
                        labelWidth={0}
                        type='number'
                    />
                </FormControl>
                ,
                measure: product.unidad_de_medida,
                actions: <IconButton aria-label="delete" onClick={() => this.deleteIngredient(product)}>
                    <DeleteIcon />
                </IconButton>
            }
            :
            ingredient = {
                id_base_product: product.id_base_product,
                quantityValue: value,
                cost: this.getIngredientCost(product.unidad_de_medida, product.precio, parseInt(value, 10)),
                quantity: <FormControl variant="outlined" style={{ width: '12ch' }}>
                    <OutlinedInput
                        fullWidth
                        id="outlined-adornment-weight"
                        /* value={values.weight} */
                        onChange={(e) => this.onChangeQuantity(e, product)}
                        endAdornment={<InputAdornment position="end">
                            {product.unidad_de_medida === 'Pieza' && 'Pz'}
                            {product.unidad_de_medida === 'Kilogramo' && 'Kg'}
                            {product.unidad_de_medida === 'Litro' && 'L'}
                        </InputAdornment>}
                        defaultValue={value}
                        labelWidth={0}
                        type='number'
                    />
                </FormControl>,
                measure: product.unidad_de_medida,
                actions: <IconButton aria-label="delete" onClick={() => this.deleteIngredient(product)}>
                    <DeleteIcon />
                </IconButton>
            }
        this.setState({
            alertQuantity: false,
            ingredients: [...this.state.ingredients, ingredient]
        })
    }

    //Delete ingredient from list
    deleteIngredient = (product) => {
        let newIngredients = this.state.ingredients.filter((ingrediente) => {
            return product.id_stock ?
                ingrediente.id_stock !== product.id_stock && ingrediente.name !== product.nombre
                :
                ingrediente.id_base_product !== product.id_base_product && ingrediente.name !== product.nombre
        })
        this.setState({
            ingredients: newIngredients
        })
    }


    getBaseProducts = () => {
        API.get('/get_stock.php')
            .then((response) => {
                response.data.status === 'success' ?
                    this.setState({
                        baseProducts: response.data.data
                    })
                    :
                    console.log('Error')
            })
            .catch((error) => {
                console.log(error)
            })
    }

    getStock = () => {
        API.get('/get_stock.php')
            .then((response) => {
                response.data.status === 'success' ?
                    this.setState({
                        stock: response.data.data
                    })
                    :
                    console.log('Error')
            })
            .catch((error) => {
                console.log(error)
            })
    }

    getCategories = () => {
        API.get('/get_categories.php')
            .then((response) => {
                response.data.status === 'success' ?
                    this.setState({
                        categories: response.data.data
                    })
                    :
                    console.log('Error')
            })
            .catch((error) => {
                console.log(error)
            })
    }

    onSubmitModal = (e) => {
        e.preventDefault();
        console.log(this.state)

    }
    handleChange = event => {
        const value = event.target.value;
        this.setState({
            ...this.state,
            [event.target.name]: value
        });
    }

    render() {
        const { modalTitle, isOpen } = this.props;
        const { productDescription, alertQuantity, productName, productCategory, categories, stockSelect, stock, baseProducts, baseProductSelect } = this.state;
        return (
            <div>
                <Modal isOpen={isOpen} size={'xl'} zIndex={1210} >
                    <ModalHeader>{modalTitle}</ModalHeader>
                    <Form onSubmit={this.onSubmitModal} autoComplete='off'>
                        <ModalBody>
                            <Container>
                                <Typography variant='subtitle2' gutterBottom>
                                    Informacin general.
                                    </Typography>
                                <Row>
                                    <Col sm='6' className='mb-3'>
                                        <ImageUpload

                                        />
                                    </Col>
                                    <Col sm='6' >
                                        <TextField className='mb-3' fullWidth id='productName' label='Nombre' name='productName' variant='outlined' onChange={this.handleChange} value={productName} required />
                                        <TextField
                                            fullWidth
                                            id='productCategory'
                                            select
                                            label='Categoria'
                                            name='productCategory'
                                            value={productCategory}
                                            onChange={this.handleChange}
                                            helperText='Selecciona una categoria'
                                            variant='outlined'
                                        >
                                            {categories && categories.map((category, key) => {
                                                return (
                                                    <MenuItem key={key} value={category.id_categoria}>
                                                        {category.nombre}
                                                    </MenuItem>
                                                );
                                            })}
                                        </TextField>
                                        <MaterialButton size='small' color='primary' style={{ textTransform: 'initial' }}>Crear categora</MaterialButton>
                                        <TextField
                                            fullWidth
                                            id="productDescription"
                                            label="Descripcin"
                                            multiline
                                            name='productDescription'
                                            rows={5}
                                            value={productDescription}
                                            onChange={this.handleChange}
                                            variant='outlined'
                                        />
                                    </Col>
                                </Row>
                                <Typography variant='subtitle2' gutterBottom>
                                    Ingredientes.
                                    </Typography>
                                <Row>
                                    <Col sm='6'>
                                        <TextField
                                            fullWidth
                                            id='stockSelect'
                                            select
                                            label='Articulos de inventario'
                                            name='stockSelect'
                                            value={stockSelect}
                                            onChange={this.handleSelectIngredient}
                                            helperText='Articulos de inventario.'
                                            variant='outlined'
                                        >
                                            {stock && stock.map((stockItem, key) => {
                                                return (
                                                    <MenuItem key={key} value={stockItem}>
                                                        {stockItem.nombre}
                                                    </MenuItem>
                                                );
                                            })}
                                        </TextField>
                                        <div className='mb-2' />
                                        <TextField
                                            fullWidth
                                            id='baseProductSelect'
                                            select
                                            label='Productos base'
                                            name='baseProductSelect'
                                            value={baseProductSelect}
                                            onChange={this.handleSelectIngredient}
                                            helperText='Productos base.'
                                            variant='outlined'
                                        >
                                            {baseProducts && baseProducts.map((base, key) => {
                                                return (
                                                    <MenuItem key={key} value={base}>
                                                        {base.nombre}
                                                    </MenuItem>
                                                );
                                            })}
                                        </TextField>
                                        <MaterialButton onClick={() => this.setState({baseProductModal: true})} size='small' color='primary' style={{ textTransform: 'initial' }}>Crear producto base</MaterialButton>
                                        <div className='mb-3' />
                                    </Col>
                                    <Col sm='6'>
                                        <ModalProductTable
                                            products={this.state.ingredients}
                                            onChangeQuantity={this.onChangeQuantity}
                                        />
                                    </Col >
                                </Row>
                            </Container>
                        </ModalBody>
                        <ModalFooter>
                            <Button color='primary' >Agregar producto</Button>{' '}
                            <Button color='secondary' onClick={this.props.cancelModal} >Cancelar</Button>
                        </ModalFooter>
                    </Form>
                </Modal>
                <AlertInput
                    modalTitle='Agregar cantidad'
                    isOpen={this.state.alertQuantity}
                    value={this.state.alertQuantityItem}
                    cancelModal={() => this.setState({ alertQuantity: false })}
                    toggle={() => this.setState({ alertQuantity: !this.state.alertQuantity })}
                    onConfirmAddProduct={this.onConfirmAddProduct}
                />
                <ModalBaseProduct
                    modalTitle='Crear producto base'
                    isOpen={this.state.baseProductModal}
                    cancelModal={() => this.setState({ baseProductModal: false })}
                    toggle={() => this.setState({ baseProductModal: !this.state.baseProductModal })}
                    onConfirmAddProduct={this.onConfirmAddProduct}
                />
            </div>
        );
    }
}