import React, { Component } from 'react';
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
import { Container, Row, Col } from 'reactstrap';
import { Form, FormGroup, Label, Input } from 'reactstrap';
import { isPossiblePhoneNumber} from 'react-phone-number-input';
import Phone from './components/Phone';
import 'react-phone-number-input/style.css';
import SweetAlert from 'react-bootstrap-sweetalert';
import API from 'utils/API';

export default class ModalProvider extends Component {
    constructor(props) {
        super(props)
        this.state = {
            providerName: '',
            providerAddress: '',
            providerPhone: '',
            isValidPhoneNumber: true,
            sweetAlert: null
        }
    }

    onSubmitModal = (e) => {
        e.preventDefault();
        if (!this.state.isValidPhoneNumber) {
            this.errorAlert('Nmero invalido', 'Digite un nmero valido')
            return
        }
        let tel = this.state.providerPhone.length > 2 ? '+' + this.state.providerPhone : ''
        let provider = {
            nombre: this.state.providerName,
            direccion: this.state.providerAddress,
            telefono: tel
        }
        API.post('/create_supplier.php', provider)
            .then(res => {
                if (res.data.status === 'fail') {
                    this.errorAlert("Error al crear proveedor", res.data.message)
                } else {
                    this.showAlert();
                    this.setState({
                        providerName: '',
                        providerAddress: '',
                        providerPhone: '',
                        isValidPhoneNumber: true,
                    })
                    this.props.cancelModal()
                }
            })
            .catch(error => {
                this.errorAlert("Error al crear proveedor", error.message);
            });
    }


    verifyValidPhone = (value) => {
        let phone = '+' + value;
        if (value.length > 2 && value.length < 12) {
            this.setState({
                isValidPhoneNumber: false
            })
        } else if (value.length === 2) {
            this.setState({
                isValidPhoneNumber: true
            })
        } else if (isPossiblePhoneNumber(phone)) {
            this.setState({
                isValidPhoneNumber: true
            })
        } else {
            this.setState({
                isValidPhoneNumber: false
            })
        }
    }

    errorAlert = (title, description) => {
        this.setState({
            sweetAlert: <SweetAlert title={title}
                danger
                onConfirm={() =>
                    this.setState({
                        sweetAlert: null
                    })
                }
                onCancel={() =>
                    this.setState({
                        sweetAlert: null
                    })
                }>
                {description}
            </SweetAlert>
        })
    }

    showAlert = () => {
        this.setState({
            sweetAlert: <SweetAlert title="Proveedor creado correctamente"
                onConfirm={() =>
                    this.setState({
                        sweetAlert: null
                    })
                }
                onCancel={() =>
                    this.setState({
                        sweetAlert: null
                    })
                }>
            </SweetAlert>
        })
    }

    handleChange = event => {
        const value = event.target.value;
        this.setState({
            ...this.state,
            [event.target.name]: value
        });
    }

    handlePhone = (value) => {
        this.verifyValidPhone(value);
        this.setState({
            providerPhone: value
        });
    }

    render() {
        const { modalTitle, isOpen } = this.props;
        const { providerAddress, providerName, providerPhone } = this.state;
        return (
            <div>
                <Modal isOpen={isOpen} zIndex={1210}  >
                    <ModalHeader>{modalTitle}</ModalHeader>
                    <Form onSubmit={this.onSubmitModal}>
                        <ModalBody>
                            <Container>
                                <Row>
                                    <Col >
                                        <FormGroup>
                                            <Label for='providerName'>Nombre </Label>
                                            <Input type='text' name='providerName' id='providerName' onChange={this.handleChange} value={providerName} autoComplete={'off'} required />
                                        </FormGroup>
                                        <FormGroup>
                                            <Label for='providerAddress'>Direccin </Label>
                                            <Input type='textarea' name='providerAddress' id='providerAddress' onChange={this.handleChange} value={providerAddress} />
                                        </FormGroup>
                                        <FormGroup>
                                            <Label for='providerAddress'>Telefono </Label><br />
                                            <Phone
                                                value={providerPhone}
                                                onChange={this.handlePhone}
                                                isValidPhoneNumber={this.state.isValidPhoneNumber}
                                            />
                                        </FormGroup>
                                    </Col>
                                </Row>
                            </Container>
                        </ModalBody>
                        <ModalFooter>
                            <Button color='primary' >Agregar proveedor</Button>{' '}
                            <Button color='secondary' onClick={this.props.cancelModal} >Cancelar</Button>
                        </ModalFooter>
                    </Form>
                </Modal>
                {this.state.sweetAlert}
            </div>
        );
    }
}