import React, { Component, forwardRef } from 'react';
import PageTitleBar from 'components/PageTitleBar';
import MUIDataTable from 'mui-datatables';
import Button from '@material-ui/core/Button';
import AddCircleIcon from '@material-ui/icons/AddCircle';
import tableConfig from './TableConfig';
import API from 'utils/API';
import ModalStock from './Components/ModalStock';
import TextField from '@material-ui/core/TextField';
import MenuItem from '@material-ui/core/MenuItem';
import DeleteIcon from '@material-ui/icons/Delete';
import AddCircleOutlineIcon from '@material-ui/icons/AddCircleOutline';
import AddBox from "@material-ui/icons/AddBox";
import ArrowDownward from "@material-ui/icons/ArrowDownward";
import MaterialTable from 'material-table';
import Snackbar from '@material-ui/core/Snackbar';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import MuiAlert from '@material-ui/lab/Alert';
import ModalAddStock from './Components/ModalAddStock'

/* import SweetAlert from 'react-bootstrap-sweetalert'; */

import SweetAlert from 'react-bootstrap-sweetalert';
import { Paper } from '@material-ui/core';


export default class StockList_ extends Component {
    constructor(props) {
        super(props);
        this.state = {
            data: [],
            isOpen: false,
            sweetAlert: null,
            allStockData: [],
            openSnackBar: false,
            snackbarSeverity: 'success',
            snackbarLabel: '',
            itemToAdd: {},
            addStockModal: false
        }
    }

    componentDidMount() {
        this.getStockData();
    }

    getStockData = async () => {
        try {
            let stockData = await API.get('/get_stock.php');
            let stock;
            if (stockData.data.status === 'success') {
                stock = stockData.data.data.map((s) => {
                    const quantityButton = <div>
                        {s.cantidad}
                        <IconButton aria-label='delete' >
                            <AddCircleOutlineIcon fontSize='small' />
                        </IconButton>
                    </div>
                    const select = <TextField
                        id={'stock' + s.id_stock}
                        name={'stock' + s.id_stock}
                        select
                        defaultValue={s.unidad_de_medida ? s.unidad_de_medida : ''}
                        /* value={s.unidad_de_medida ? s.unidad_de_medida : 'Kilogramo' } */
                        onChange={(e) => this.handleSelect(e, s)}
                        variant='outlined'
                        size='small'
                    >
                        {['Kilogramo', 'Litro', 'Pieza'].map((option, key) => (
                            <MenuItem key={key} value={option}>
                                {option}
                            </MenuItem>
                        ))}
                    </TextField>
                    return [
                        s.id_stock,
                        s.nombre,
                        quantityButton,
                        s.descripcion,
                        s.precio,
                        '',
                        s.proveedor,
                        s.fecha_de_compra,
                        s.fecha_de_caducidad,
                        s.dias_de_soporte,
                        select
                    ]
                })
            }
            this.setState({
                data: stockData.data.data
            })

        } catch (error) {
            console.log(error)
        }
    }

    getResponseError = (error) => {
        if (error.response) {
            // Request made and server responded
            console.log(error.response.data);
            console.log(error.response.status);
            console.log(error.response.headers);
            return error.response.data
        } else if (error.request) {
            // The request was made but no response was received
            console.log(error.request);
            return error.request
        } else {
            // Something happened in setting up the request that triggered an Error
            console.log('Error', error.message);
            return error.message
        }
    }

    render() {
        const columns = tableConfig.columns
        const data = this.state.data

        return (
            <div>
                <PageTitleBar title={'Inventario'} description={'Inventario de articulos'} />
                <div className={'m-3'}>
                    <div className={'m-2'}>
                        <div className='mb-3'>
                            <Button
                                variant='contained'
                                color='secondary'
                                startIcon={<AddCircleIcon />}
                                onClick={() => { this.setState({ isOpen: true }) }}
                            >
                                Crear nuevo articulo.
                        </Button>
                        </div>

                        <MaterialTable
                            title="Inventario"
                            columns={columns}
                            data={data}
                            icons={tableConfig.tableIcons}
                            options={tableConfig.options}
                            localization={tableConfig.localization}
                            actions={[
                                rowData => ({
                                    icon: forwardRef((props, ref) => <AddBox {...props} ref={ref} />),
                                    tooltip: 'Agregar articulos',
                                    onClick: (event, rowData) => {
                                        this.setState({
                                            addStockModal: true,
                                            itemToAdd: rowData
                                        })
                                    }
                                })
                            ]
                            }
                            /* detailPanel={tableConfig.detailPanel} */
                            editable={{
                                onRowAdd: (newData) =>
                                    new Promise((resolve) => {
                                        setTimeout(() => {
                                            resolve();
                                            API.post('create_stock_item.php', newData)
                                                .then((response) => {
                                                    this.setState((prevState) => {
                                                        const data = [...prevState.data];
                                                        data.push(newData);
                                                        return { ...prevState, data };
                                                    }, () => this.setState({
                                                        openSnackBar: true,
                                                        snackbarSeverity: 'success',
                                                        snackbarLabel: 'Articulo creado correctamente'
                                                    })
                                                    );
                                                })
                                                .catch((error) => {
                                                    console.log(error)
                                                    this.setState({
                                                        openSnackBar: true,
                                                        snackbarSeverity: 'error',
                                                        snackbarLabel: 'Ocurrio un problema: ' + error + this.getResponseError(error)
                                                    })
                                                })

                                        }, 600);
                                    }),
                                onRowUpdate: (newData, oldData) =>
                                    new Promise((resolve) => {
                                        setTimeout(() => {
                                            resolve();
                                            if (oldData) {
                                                API.post('/update_stock_item.php', newData)
                                                    .then((response) => {
                                                        console.log(response)
                                                        this.setState((prevState) => {
                                                            const data = [...prevState.data];
                                                            data[data.indexOf(oldData)] = newData;
                                                            return { ...prevState, data };
                                                        }, () => this.setState({
                                                            openSnackBar: true,
                                                            snackbarSeverity: 'success',
                                                            snackbarLabel: 'Articulo editado correctamente'
                                                        })
                                                        );
                                                    })
                                                    .catch((error) => {
                                                        console.log(error)
                                                        this.setState({
                                                            openSnackBar: true,
                                                            snackbarSeverity: 'error',
                                                            snackbarLabel: 'Ocurrio un problema: ' + error + this.getResponseError(error)
                                                        })
                                                    })

                                            }
                                        }, 600);
                                    }),
                                onRowDelete: (oldData) =>
                                    new Promise((resolve) => {
                                        setTimeout(() => {
                                            resolve();
                                            API.delete('delete_stock_item.php', { data: { id_stock: oldData.id_stock } })
                                                .then((response) => {
                                                    console.log(response)
                                                    this.setState((prevState) => {
                                                        const data = [...prevState.data];
                                                        data.splice(data.indexOf(oldData), 1);
                                                        return { ...prevState, data };
                                                    }, () => this.setState({
                                                        openSnackBar: true,
                                                        snackbarSeverity: 'success',
                                                        snackbarLabel: 'Articulo eliminado correctamente'
                                                    })
                                                    );
                                                })
                                                .catch((error) => {
                                                    console.log(error)
                                                    this.setState({
                                                        openSnackBar: true,
                                                        snackbarSeverity: 'error',
                                                        snackbarLabel: 'Ocurrio un problema: ' + error + this.getResponseError(error)
                                                    })
                                                })
                                        }, 600);
                                    }),
                            }}
                        />
                    </div>
                </div>
                {this.state.sweetAlert}

                <ModalStock
                    isOpen={this.state.isOpen}
                    modalTitle={'Crear un nuevo articulo'}
                    cancelModal={() => { this.setState({ isOpen: false }, () => this.getStockData()) }}
                />
                <ModalAddStock
                    isOpen={this.state.addStockModal}
                    cancelModal={() => this.setState({ addStockModal: false }, () => this.getStockData())}
                    modalTitle='Agregar cantidad a articulo'
                    item={this.state.itemToAdd}
                />
                <Snackbar
                    anchorOrigin={{
                        vertical: 'bottom',
                        horizontal: 'left',
                    }}
                    open={this.state.openSnackBar}
                    autoHideDuration={6000}
                    onClose={() => this.setState({ openSnackBar: false })}
                    message={this.state.snackbarLabel}

                    action={
                        <React.Fragment>
                            <IconButton size="small" aria-label="close" color="inherit" onClick={() => this.setState({ openSnackBar: false })}>
                                <CloseIcon fontSize="small" />
                            </IconButton>
                        </React.Fragment>
                    }
                >
                    <MuiAlert elevation={6} variant="filled" severity={this.state.snackbarSeverity}>{this.state.snackbarLabel}</MuiAlert>
                </Snackbar>
            </div>
        );
    }
}