import React, { Component } from 'react';
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
import { Container, Row, Col } from 'reactstrap';
import { Form, FormGroup, Label, Input, FormText } from 'reactstrap';
import Required from 'components/Required';
import { default as MaterialButton } from '@material-ui/core/Button';
import ModalProvider from 'components/ModalProvider'
import API from 'utils/API';
import SweetAlert from 'react-bootstrap-sweetalert';


export default class ModalStock extends Component {

    constructor(props) {
        super(props);
        this.state = {
            itemName: '',
            itemDescription: '',
            idProvider: '',
            unit: '',
            price: '',
            supportDays: '',
            modalProvider: false,
            providerInfo: [],
            sweetAlert: null
        }
    }

    componentDidMount() {
        this.getProvidersInfo()
    }

    getProvidersInfo = () => {
        API.get('/get_suppliers.php')
            .then((res) => {
                console.log(res.data)
                this.setState({
                    providerInfo: res.data.data
                })
            })
            .catch((error) => {
                console.log(error)
            })
    }

    cancelProviderModal = () => {
        this.setState({
            modalProvider: false
        })
        this.getProvidersInfo();
    }

    handleChange = event => {
        const value = event.target.value;
        this.setState({
            ...this.state,
            [event.target.name]: value
        });
    }

    errorAlert = (title, description) => {
        this.setState({
            sweetAlert: <SweetAlert title={title}
                danger
                onConfirm={() =>
                    this.setState({
                        sweetAlert: null
                    })
                }
                onCancel={() =>
                    this.setState({
                        sweetAlert: null
                    })
                }>
                {description}
            </SweetAlert>
        })
    }

    showAlert = () => {
        this.setState({
            sweetAlert: <SweetAlert title="Proveedor creado correctamente"
                onConfirm={() =>
                    this.setState({
                        sweetAlert: null
                    })
                }
                onCancel={() =>
                    this.setState({
                        sweetAlert: null
                    })
                }>
            </SweetAlert>
        })
    }

    onSubmitModal = (e) => {
        e.preventDefault();
        console.log(this.state);
        let stockItem = {
            nombre: this.state.itemName,
            id_proveedor: this.state.idProvider,
            descripcion: this.state.itemDescription,
            precio: this.state.price,
            dias_de_soporte: this.state.supportDays,
            unidad_de_medida: this.state.unit
        }
        API.post('create_stock_item.php', stockItem)
            .then(res => {
                console.log(res)
                if (res.data.status === 'fail') {
                    this.errorAlert("Error al crear proveedor", res.data.message)
                } else {
                    this.showAlert();
                    this.setState({
                        itemName: '',
                        itemDescription: '',
                        idProvider: '',
                        unit: '',
                        price: '',
                        supportDays: ''
                    })
                    this.props.cancelModal()
                }
            })
            .catch(error => {
                console.log(error)
                this.errorAlert("Error al crear proveedor", error.message);
            });
    }
    render() {
        let { isOpen, modalTitle } = this.props
        return (
            <div>
                {this.state.sweetAlert}
                <Modal isOpen={isOpen} zIndex={1200} size='lg' >
                    <ModalHeader>{modalTitle}</ModalHeader>
                    <Form onSubmit={this.onSubmitModal}>
                        <ModalBody>
                            <Container>
                                <Row>
                                    <Col md='6'>
                                        <FormGroup>
                                            <Label for='itemName'>Nombre <Required placement='right' /></Label>
                                            <Input required type='text' name='itemName' id='itemName' onChange={this.handleChange} value={this.state.itemName} />
                                        </FormGroup>
                                        <FormGroup>
                                            <Label for='itemDescription'>Descripcin </Label>
                                            <Input type='textarea' name='itemDescription' id='itemDescription' onChange={this.handleChange} value={this.state.itemDescription} />
                                        </FormGroup>
                                        <FormGroup>
                                            <Label for="supportDays">Das de soporte</Label>
                                            <Input
                                                type="number"
                                                name="supportDays"
                                                id="supportDays"
                                                onChange={this.handleChange}
                                            />
                                        </FormGroup>
                                    </Col>
                                    <Col md='6'>
                                        <FormGroup style={{ marginBottom: '0px' }}>
                                            <Label for='idProvider'>Proveedor</Label>
                                            <Input type='select' name='idProvider' id='idProvider' value={this.state.idProvider} onChange={this.handleChange}>
                                                <option value={''}>Seleccionar proveedor</option>
                                                {
                                                    this.state.providerInfo && this.state.providerInfo.map((provider, key) => {
                                                        return <option value={provider.id_supplier} key={key}>{provider.nombre}</option>
                                                    })
                                                }
                                            </Input>
                                            <FormText color="muted">
                                                <MaterialButton onClick={() => this.setState({ modalProvider: true })} disableFocusRipple={true} size='small' color='primary' style={{ textTransform: 'none' }}>
                                                    Agregar un proveedor
                                                </MaterialButton>
                                            </FormText>
                                        </FormGroup>

                                        <FormGroup>
                                            <Label for="price">Precio</Label>
                                            <Input
                                                type="number"
                                                name="price"
                                                id="price"
                                                step="any"
                                                onChange={this.handleChange}
                                            />
                                        </FormGroup>
                                        <FormGroup>
                                            <Label for='unit'>Unidad de medida</Label>
                                            <Input type='select' name='unit' id='unit' value={this.state.unit} onChange={this.handleChange} required>
                                                <option value={'Kilogramo'}>Kilogramo</option>
                                                <option value={'Pieza'}>Pieza</option>
                                                <option value={'Litro'}>Litro</option>
                                            </Input>
                                        </FormGroup>
                                    </Col>
                                </Row>
                            </Container>
                        </ModalBody>
                        <ModalFooter>
                            <Button color='primary' >Crear artculo</Button>{' '}
                            <Button color='secondary' onClick={this.props.cancelModal} >Cancelar</Button>
                        </ModalFooter>
                    </Form>
                </Modal>
                <ModalProvider modalTitle='Proveedor' isOpen={this.state.modalProvider} cancelModal={this.cancelProviderModal} />
            </div>
        );

    }
}